.hero_section {
	display: flex;
	height: 90%;
	margin-right: 20px;
	flex-direction: column;
	width: 100%;
	justify-content: center;
	text-align: right;
	overflow: none;
}

.hero_img {
	position: relative;
	z-index: 1;
	max-width: 100%;
	max-height: 100%;
}

.hero_content {
	position: absolute;
	display: flex;
	height: 90%;
	margin-right: 20px;
	flex-direction: column;
	width: 95%;
	justify-content: center;
	text-align: right;
	z-index: 2;
}

.hero_content h1 {
	color: var(--contrast-color);
	font-weight: bolder;
	font-size: 70px;
}
.hero_content h2 {
	color: var(--second-background-color);
	font-weight: bolder;
	align-self: right;
	font-size: 50px;
	padding-left: 60%;
	margin: 35px 0px;
}

.hero_content button {
	align-self: right;
	padding: 10px;
	font-size: 30px;
	border-radius: 0.5em;
	background-color: var(--highlight-color);
	color: var(--header-footer-color);
	font-weight: bolder;
}

.hero_content button:hover,
.hero_content button:focus {
	box-shadow: 2px 2px 2px 2px var(--main-background-color);
	-webkit-transform: translateY(-0.25em);
	        transform: translateY(-0.25em);
}

@media screen and (max-width: 1234px) {
	.hero_content {
		height: 70%;
	}
	.hero_content h1 {
		font-size: 45px;
	}
	.hero_content h2 {
		font-size: 35px;
	}
}

@media screen and (max-width: 968px) {
	.hero_content {
		height: 50%;
	}
	.hero_content h1 {
		font-size: 30px;
	}
	.hero_content h2 {
		font-size: 20px;
	}
}

@media screen and (max-width: 744px) {
	.hero_content {
		height: 30%;
	}

	.hero_content h1 {
		font-size: 30px;
	}
	.hero_content h2 {
		font-size: 20px;
	}
	.hero_content button {
		align-self: right;
		padding: 10px;
		font-size: 20px;
		border-radius: 0.5em;
		background-color: var(--highlight-color);
		color: var(--header-footer-color);
		font-weight: bolder;
	}
}

@media screen and (max-width: 504px) {
	.hero_content h1 {
		font-size: 25px;
	}
	.hero_content h2 {
		font-size: 16px;
	}
	.hero_content button {
		padding: 8px;
		font-size: 12px;
	}
}

@media screen and (max-width: 390px) {
	.hero_content h2 {
		margin: 10px 0px;
	}
}

/* Start About Me Section */
.aboutMe {
	background-color: var(--second-background-color);
	display: flex;
}
.aboutMe img {
	margin: var(--outside-padding);
	max-width: 20%;
	border-radius: 0.5rem;
}
.about_me_content {
	display: inline-block;
	margin: var(--outside-padding);
	color: var(--header-footer-color);
	font-size: large;
}
.aboutMe h4 {
	margin: 10px 0px;
	font-size: 25px;
	font-weight: bolder;
}

.about_me_content h2 {
	font-size: xxx-large;
	font-weight: bold;
	margin-bottom: 3%;
}
.aboutMe ul{
	margin-bottom: 10px;
}

@media screen and (max-width: 968px) {
	#aboutMe {
		flex-wrap: wrap;
		justify-content: center;
	}
	#aboutMe img {
		max-width: 50%;
	}
}
.resume_container {
	background-color: var(--second-background-color);
	/* height: 75vh; */
	padding: 60px 0px;
}

.skill_set {
	background-color: var(--highlight-color);
	margin: 0px 5% 60px;
	border-radius: 1rem;
}

.skill_set h3 {
	font-size: 30px;
	padding: 30px;
}

.skill_list {
	display: flex;
	justify-content: space-around;
	padding: 20px;
}
.skill_li {
	margin-bottom: 20px;
	font-size: 25px;
}

.technologies_container {
	background-color: var(--highlight-color);
	margin: 0px 5%;
	border-radius: 1rem;
}
.technologies_container h3 {
	font-size: 30px;
	padding: 30px;
	margin-bottom: 30px;
}

.technologies {
	font-size: xx-large;
	font-weight: 500;
}
.technologies_li {
	margin-left: 5%;
    padding-bottom: 20px;
    font-size: 25px;
}

.download_Link {
	background-color: var(--highlight-color);
	margin: 60px 5% 0px;
	border-radius: 1rem;
	display: flex;
	justify-content: center;
}

.download_aTag {
	color: black;
	font-size: 40px;
	font-weight: bold;
	margin: 20px;
	transition: text-shadow 1s;
}

.download_aTag:hover {
	text-shadow: 1px 1px 5px black, 0 0 0.1em rgb(5, 5, 5), 0 0 0.5em black;
	color: var(--main-background-color);
	color: white;
}

@media screen and (max-width: 900px) {
	.skill_list {
		flex-wrap: wrap;
		justify-content: left;
    }
}

/* Start Contact Information */
.contactMe {
	background-color: var(--second-background-color);
	padding: var(--outside-padding);
	display: flex;
}
.contactMe h2 {
	color: var(--header-footer-color);
	font-weight: bolder;
	font-size: 50px;
}
.contactMe form {
	background-color: var(--highlight-color);
	flex-direction: column;
	width: 100%;
	margin-right: var(--outside-padding);
	padding: 1% 2% 2% 2%;
	justify-content: space-around;
	display: flex;
	border-radius: 1rem;
}

.input_group {
	display: flex;
	width: 100%;
	justify-content: space-between;
}
.label {
	display: flex;
	width: 100%;
	justify-content: space-between;
}

.input {
	background: var(--main-background-color);
	border: none;
	color: aliceblue;
	padding: 10px 10px 5px 5px;
	font-size: 30px;
	border-radius: 0.5rem;
	margin:3px;
}
.input::-webkit-input-placeholder {
	color: aliceblue;
	font-size: 30px;
}
.input::placeholder {
	color: aliceblue;
	font-size: 30px;
}

textarea {
	background: var(--main-background-color);
	border: none;
	color: aliceblue;
	padding: 16px 178px 100px 5px;
	font-size: 30px;
	border-radius: 0.5rem;
}

textarea::-webkit-input-placeholder {
	color: aliceblue;
}

textarea::placeholder {
	color: aliceblue;
}

.input_group label {
	font-weight: bold;
	font-size: 30px;
}

.contactMe img {
	max-width: 40%;
	border-radius: 0.5rem;
}

.error-text{
	margin-top: 20px;
}

.contactMe button {
	margin-top: 20px;
	background: var(--contrast-color);
	color: var(--header-footer-color);
	border-radius: 0.5rem;
	font-size: 30px;
	width: 30%;
}

.contactMe button:hover,
.contactMe button:focus {
	background-color: var(--header-footer-color);
	color: var(--contrast-color);
}

.aTag {
	padding-top:20px;
	color: black;
	font-size: 25px;
}

.aTag a:hover{
	color:blue;
}

@media screen and (max-width: 1288px) {
	.input {
		padding: 10px 90px 2px 2px;
		font-size: 20px;
	}
	.input::-webkit-input-placeholder {
		font-size: 20px;
	}
	.input::placeholder {
		font-size: 20px;
	}
	.input_group label {
		font-size: 20px;
	}
	textarea {
		padding: 16px 100px 60px 5px;
		font-size: 20px;
	}
}

@media screen and (max-width: 1048px) {
