/* Start Portfolio Section */
.portfolio {
	background: var(--main-background-color);
	padding: 50px var(--outside-padding);
	color: #f0f8ff;
}

/* .portfolio img {
	opacity: 1;
}

.portfolio img:hover {
	opacity: 0;
} */

.portfolio h2 {
	margin-bottom: 50px;
	font-weight: bolder;
	font-size: xxx-large;
}

.project_group_portfolio {
	display: flex;
	flex-wrap: wrap;
	grid-gap: 80px;
	gap: 80px;
}

.projects {
	width: 47%;
}

.projects img {
	width: 100%;
	height: 100%;
	border-radius: 0.5rem;
}

.projects .project_box {
	position: absolute;
	z-index: 2;
	background-color: var(--header-footer-color);
	margin-top: -87px;
	padding: 30px;
	border-radius: 0px 0.5rem 0px 0.5rem;
}

.projects .proTitle {
	font-size: 25px;
}
.projects p {
	margin-top: 10px;
	font-size: 20px;
}

.overlay {
	position: absolute;
	margin-left: 0.2%;
	width: 42%;
	z-index: 0;
	height: 450px;
	margin-top: -450px;
	border-radius: 0.5rem;
	opacity: 0;
	color: white;
	background-color: #000000dd;
}
.overlay:hover {
	opacity: 1;
}

.overlayText {
	font-size: 20px;
	padding: 10px;
}

.overlayTitle {
	font-weight: bolder;
	font-size: larger;
}

.overlayLinks{
	margin-top: 20px;
	display: flex;
	justify-content: space-evenly;
	font-size: xx-large;
	font-weight: bold;
}
/* .overlayLinks:hover{
	color:blue;
} */


