@media screen and (max-width: 504px) {
	.projects .project_box {
		margin-top: -23px;
		padding: 5px;
	}

	.projects .proTitle {
		font-size: 12px;
	}
	.projects p {
		margin-top: 6px;
		font-size: 8px;
	}
}

:root {
	--main-background-color: #808080;
	--header-footer-color: #542344;
	--contrast-color: #d9d9d9;
	--highlight-color: #bfd1e5;
	--second-background-color: #d8bfaa;
	--outside-padding: 5%;
}

* {
	font-family: 'Inter', sans-serif;
	box-sizing: border-box;
}

/* Start header section */
.header {
	display: flex;
	background-color: #542344;
	background-color: var(--header-footer-color);
	justify-content: space-between;
	align-items: center;
	width: 100%;
	padding: 3% 5%;
	padding: 3% var(--outside-padding);
}

.nav_list li {
	display: inline;
}

.nav_item {
	padding: 10px;
	font-size: x-large;
	text-decoration: none;
}

.nav_link:hover {
	font-weight: bolder;
	color: #d8bfaa;
	color: var(--second-background-color);
}
.active {
	font-weight: bolder;
	color: #d8bfaa;
	color: var(--second-background-color);
}

.logo_container {
	color: #d9d9d9;
	color: var(--contrast-color);
	text-decoration: none;
	font-size: xxx-large;
	font-weight: 900;
	transition: text-shadow 1s;
}

.logo_container:hover {
	text-shadow: 1px 1px 5px black, 0 0 0.1em white, 0 0 0.5em white;
	color: #808080;
	color: var(--main-background-color);
	color: black;
}
/* End header section */

/* This is where the footer starts */
footer {
	background-color: #542344;
	background-color: var(--header-footer-color);
	width: 100%;
}
footer ul {
	display: flex;
	background-color: #542344;
	background-color: var(--header-footer-color);
	justify-content: center;
	color: #d9d9d9;
	color: var(--contrast-color);
	font-size: x-large;
}

footer li {
	text-decoration: none;
	padding: 20px;
	list-style-type: none;
}

.footer {
	text-decoration: none;
}
.footer:hover {
	font-weight: bolder;
	color: #d8bfaa;
	color: var(--second-background-color);
}

.svgGit {
	vertical-align: -0.125em;
	width: 2em;
	height: 2em;
}

.svgGit:hover {
	fill: #d8bfaa;
	fill: var(--second-background-color);
}

.svgLinkedIn {
	vertical-align: -0.125em;
	width: 2em;
	height: 2em;
}

.svgLinkedIn:hover {
	fill: #d8bfaa;
	fill: var(--second-background-color);
}
.medium {
	font-weight: bold;
	color: white;
	font-size: 54px;
}

@media screen and (max-width: 825px) {
	footer {
		font-size: large;
	}
	.nav_item {
		font-size: 16px;
		margin: 0px;
	}
}

@media screen and (max-width: 686px) {
	.nav_item {
		font-size: 12px;
		margin: 0px;
	}
	.logo_container {
		font-size: x-large;
	}
}

@media screen and (max-width: 390px) {
	.nav_list li {
		display: flex;
	}
	.nav_item {
		margin-bottom: 3px;
		font-size: 16px;
	}
}

